<template>
<div class="login_container" @keyup.enter="handleLoginEvent">
  <div class="login_box" style="height:450px" >
    <div class="margin-top-50">
      <!-- <img v-show="showLogo()" :src="require('../assets/img/Login/login_icon_Shattrath.png')" alt="沙塔信息技术有限公司" />
      <img class="lzLogo" :src="require('../assets/img/Login/supos.png')"  /> -->

      <!-- <img :class="logoClass()" :src="logoImg()"  /> -->
    </div>
    <div class="margin-bottom-30">
      <h1>用户登录</h1>
    </div>
    <div class="margin-bottom-20">
      <el-input ref="accountInput" class="width-360 login-input" suffix-icon="login-account" placeholder="用户名" v-model.trim="loginAccount" clearable></el-input>
    </div>
    <div class="margin-bottom-15">
      <el-input type="password" ref="passwordInput" class="width-360 login-input" suffix-icon="login-password" placeholder="密码" v-model.trim="loginPassword" clearable></el-input>
    </div>
    <div class="margin-bottom-40 display-grid">
      <!-- <el-checkbox class="justify-self-start" v-model="isPasswordKept">记住密码</el-checkbox> -->
      <div class="justify-self-start">
        <el-link @click="toRegister" >立即注册</el-link>
      </div>
      <div class="justify-self-end">
        <el-link>忘记密码？</el-link>
      </div>
    </div>
    <div class="btnClass">
      <el-button class="width-360" type="primary" @click="handleLoginEvent">立即登录</el-button>
      <el-button class="width-360" type="text" @click="toCaptchaLogin">短信验证码登录</el-button>
    </div>
  </div>
</div>
</template>

<script>
// eslint-disable-next-line camelcase
import { IS_Mk, LOGO } from '@/utils/ajaxConfigure';
import {
  showErrorNotify
} from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE
} from '@/utils/constantsFile';
// 导入所需接口
import {
  login
  // logout
} from '@/api/public';
// import { showErrorMessage } from '@/utils/message';
export default {
  name: 'Login',

  data() {
    return {
      // 用户名
      loginAccount: '',

      // 密码
      loginPassword: '',

      // 是否记住密码
      isPasswordKept: true
    };
  },

  methods: {
    logoImg() {
      return require('../assets/img/Login/login_' + LOGO + '.png');
    },
    logoClass() {
      if (LOGO === 'SUPOS') {
        return 'lzLogo';
      } else if (LOGO === 'YIY') {
        return 'ylLogo';
      } else {
        return '';
      }
    },
    showLogo() {
      // eslint-disable-next-line camelcase
      if (IS_Mk) {
        return false;
      }
      if (localStorage.getItem('isSupplier') === 'true') {
        return false;
      } else {
        return true;
      }
    },
    /**
     * @description: “立即登录”按钮点击事件
     * @param 无
     * @return: void
     * @author: cyb
     */
    handleLoginEvent() {
      // 提示用户名不能为空
      if (!this.loginAccount) {
        this.$message({
          showClose: true,
          message: '用户名不能为空！',
          type: 'warning'
        });
        this.$refs.accountInput.focus();
        return false;
      }

      // 提示密码不能为空
      if (!this.loginPassword) {
        this.$message({
          showClose: true,
          message: '密码不能为空！',
          type: 'warning'
        });
        this.$refs.passwordInput.focus();
        return false;
      }
      this.login();
    },
    async login() {
      try {
        this.tableLoading = true;
        const res = await login({}, {
          userName: this.loginAccount,
          password: this.loginPassword
        });
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          localStorage.setItem('token', JSON.stringify(res.rows.token));
          localStorage.setItem('realName', JSON.stringify(res.rows.realName));
          localStorage.setItem('userId', JSON.stringify(res.rows.userId));
          localStorage.setItem('account', JSON.stringify(res.rows.userName));
          localStorage.setItem('company', res.rows.companyName);
          localStorage.setItem('companyId', res.rows.companyId);
          localStorage.setItem('mesToken', res.rows.mesToken);
          localStorage.setItem('mesTokenHeader', res.rows.mesTokenHeader);
          this.$router.push('/');
        } else {
          // 提示异常信息
          this.$message({
            showClose: true,
            message: res.message,
            type: 'warning'
          });
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {}
    },
    toCaptchaLogin() {
      this.$router.push({
        path: '/CaptchaLoginPage'
      });
    },
    toRegister() {
      this.$router.push({
        path: '/register'
      });
    },
    // 清除token
    clearToken() {
      const Token = window.localStorage.getItem('token');
      if (Token) {
        window.localStorage.removeItem('token');
      }
      // Token && logout({
      //   token: Token.replace(/"/g, '')
      // }).then(res => {
      //   if (res.code === '0000') {
      //     window.localStorage.removeItem('token');
      //   }
      // });
    }
  },
  created() {
    // this.clearToken();
  },
  mounted() {
    // 用户名输入框自动获取焦点
    this.$refs.accountInput.focus();
  }
};
</script>

